import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { VendorManagerService } from '../vendor-manager.service';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html'
})
export class UserFormComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  vendorForm: FormGroup;
  progressing = false;
  error: string;

  @Input()
  operation: 'CREATE' | 'UPDATE' = "CREATE";

  @Input()
  vendor: any;

  constructor(private router: Router, private formBuilder: FormBuilder, private vendorService: VendorManagerService) {

  }

  ngOnInit(): void {
    console.log('vendor: ', this.vendor);
    
    if (this.operation === 'UPDATE') {
      this.vendorForm = this.formBuilder.group({
        _id: this.vendor._id,
        name: [this.vendor['name'], Validators.required],
        email: [this.vendor['email'], [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]],
        address: [this.vendor['address'], Validators.required],
        city: [this.vendor['city'], Validators.required],
        mobile: [this.vendor['mobile'], [Validators.required, Validators.pattern(/^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$/)]],
        status: [this.vendor['status'], Validators.required]
      });
    } else {
      this.vendorForm = this.formBuilder.group({
        name: [this.vendor['name'], Validators.required],
        email: [this.vendor['email'], [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]],
        password: ['', [Validators.required, Validators.minLength(6), Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/)]],
        confirmPassword: ['', Validators.required],
        address: [this.vendor['address'], Validators.required],
        city: [this.vendor['city'], Validators.required],
        mobile: [this.vendor['mobile'], [Validators.required, Validators.pattern(/^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$/)]],
        status: [this.vendor['status'], Validators.required]
      });
    }
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  submit(): void {
    this.progressing = true;
    console.log('Vendor Form Values: ', this.vendorForm.value);

    if (this.operation == 'UPDATE') {
      this.vendorService.updateVendor(this.vendorForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
            this.progressing = false;
            console.log('data: ', data);
            this.router.navigate(['/vendor/detail', this.vendor['_id']]);
          },
          error: (data: any) => {
            console.log('data: ', data);
            this.progressing = false;
          }
        });
    } else {
      this.vendorService.createVendor(this.vendorForm.value)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
            this.progressing = false;
            console.log('data: ', data);
            this.router.navigate(['/vendor']);
          },
          error: (data: any) => {
            console.log('data: ', data);
            this.progressing = false;
          }
        });
    }
  }
}
