import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserListComponent } from './user-list/user-list.component';
import { UserDetailComponent } from './detail/user-detail.component';
import { CreateUserComponent } from './create/user-create.component';
import { VendorManagerComponent } from './vendor-manager.component';
import { VendorDetailResolver } from './detail/vendor-detail-resolver.service';
import { EditUserComponent } from './edit-user/edit-user.component';


const routes: Routes = [
  {
    path: '',
    component: VendorManagerComponent,
    children: [
      { path: '', redirectTo: 'list', pathMatch: 'full' },

      {
        path: 'list',
        component: UserListComponent
      },
      {
        path: 'create',
        component: CreateUserComponent
      },
      {
        path: 'edit/:id',
        component: EditUserComponent,
        resolve: { vendor: VendorDetailResolver }
      },
      {
        path: 'detail/:id',
        component: UserDetailComponent,
        resolve: { vendor: VendorDetailResolver }
      },
      { path: '**', redirectTo: 'list' }

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [VendorDetailResolver]
})
export class VendorManagerRoutingModule { }
