import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { VendorManagerService } from '../vendor-manager.service';
import { empty } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable()
export class VendorDetailResolver implements Resolve<any> {
  constructor(private vendorManagerService: VendorManagerService) { }

  resolve(route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    console.log('router', route.params);
    return this.vendorManagerService.getVendor(route.params['id']).pipe(
      catchError((error) => {
        return empty();
      })
    );
  }
}
