import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { VendorService } from '../core/vendor.service';

@Injectable()
export class VendorManagerService {

  private _vendorList$ = new BehaviorSubject<any[]>(null);
  get vendorList$(): Observable<any[]> {
    return this._vendorList$.asObservable();
  }

  constructor(private vendorService: VendorService) { }

  createVendor(user: any): Observable<any> {
    const vendor = {
      name: user['name'],
      email: user['email'],
      password: user['password'],
      address: user['address'],
      city: user['city'],
      mobile: user['mobile'],
      status: user['status']
    };
    return this.vendorService.createVendor(vendor);
  }

  updateVendor(user: any): Observable<any> {
    const vendor = {
      _id: user['_id'],
      name: user['name'],
      email: user['email'],
      address: user['address'],
      city: user['city'],
      mobile: user['mobile'],
      status: user['status']
    };
    return this.vendorService.updateVendor(vendor);
  }

  deleteVendor(id: string): Observable<any> {
    return this.vendorService.deleteVendor(id);
  }

  getVendor(id: string): Observable<any> {
    return this.vendorService.getVendor(id);
  }


  getVendorList(): void {
    this.vendorService.getVendorList()
      .subscribe((data: any[]) => {
        console.log('Got Vendors for you: ', data);

        this._vendorList$.next(data);
      });
  }
}
